*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  cursor: pointer;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

select {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
}

select::-ms-expand {
  display: none;
}

@font-face {
  src: local(Roboto), url("Roboto-Regular.ca197847.ttf");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}

@font-face {
  src: local(Roboto), url("Roboto-Medium.91322885.ttf");
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}

@font-face {
  src: local(Roboto), url("Roboto-Bold.fdb9b54a.ttf");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}

:root {
  --accent-color: #f87719;
  --dark-color: #111;
  --dark-grey-color: #282828;
  --middle-grey-color: #595959;
  --light-grey-color: #b7b7b7;
  --light-color: #f8f8f8;
  --lightest-color: #fff;
  --linear-light-to-dark: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --linear-dark-to-light: linear-gradient(141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91%);
  --primary-font: "Roboto", sans-serif;
  --transition-dur-and-fun: 250ms cubic-bezier(.4, 0, .2, 1);
}

body {
  font-family: var(--primary-font);
  color: var(--light-color);
  background-color: var(--dark-color);
  font-size: 14px;
  font-style: normal;
}

.no-scroll {
  overflow: hidden;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

.dark {
  background-color: var(--dark-color);
  color: var(--lightest-color);
}

.light {
  background-color: var(--lightest-color);
  color: var(--middle-grey-color);
}

h1 {
  font-size: 32px;
  font-weight: 700;
  line-height: 38px;
}

h2 {
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 500;
  line-height: 19px;
}

@media screen and (min-width: 768px) {
  h1 {
    font-size: 44px;
    line-height: 52px;
  }

  h2 {
    font-size: 24px;
    line-height: 28px;
  }
}

@media screen and (min-width: 1280px) {
  h1 {
    font-size: 64px;
    line-height: 75px;
  }

  h2 {
    font-size: 28px;
    line-height: 33px;
  }
}

a.btn {
  text-align: center;
  text-decoration: none;
}

.btn {
  background: var(--linear-light-to-dark) repeat scroll 0 0 rgba(0, 0, 0, 0);
  box-shadow: 0 4px 5px -6px var(--dark-color);
  border: 0;
  border-radius: 74px;
  align-items: center;
  padding: 1px;
  display: inline-flex;
  overflow: hidden;
}

.btn-in {
  width: 100%;
  height: 100%;
  color: var(--dark-color);
  background-color: inherit;
  transition: color var(--transition-dur-and-fun), background-color var(--transition-dur-and-fun);
  border-radius: 74px;
  padding: 12px 24px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.17;
  display: block;
}

.btn-accent:hover .btn-in, .btn-accent:focus .btn-in {
  color: var(--accent-color);
  background-color: var(--lightest-color);
}

.btn-dark .btn-in {
  color: var(--lightest-color);
  background-color: var(--dark-color);
}

.btn-dark:hover .btn-in, .btn-dark:focus .btn-in {
  color: var(--accent-color);
  background-color: var(--lightest-color);
}

.btn-light .btn-in {
  color: var(--dark-color);
  background-color: var(--lightest-color);
}

.btn-light:hover .btn-in, .btn-light:focus .btn-in {
  color: var(--accent-color);
  background-color: var(--dark-color);
}

.btn-round {
  width: 24px;
  height: 24px;
}

.btn-round .btn-in {
  padding: 6px;
  font-size: 10px;
  font-weight: 400;
  line-height: 1;
}

@media screen and (min-width: 768px) {
  .btn-in {
    font-size: 14px;
    line-height: 1.14;
  }

  .btn-round {
    width: 40px;
    height: 40px;
  }

  .btn-round .btn-in {
    padding: 12px;
    font-size: 14px;
  }
}

@media screen and (min-width: 1280px) {
  .btn-in {
    font-size: 16px;
    line-height: 1.5;
  }
}

.container {
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 320px) {
  .container {
    max-width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}



.rating {
  display: flex;
}

.star, .hero > .star {
  width: 14px;
  height: 14px;
}

.star:not(:last-child) {
  margin-right: 4px;
}

@media screen and (min-width: 768px) {
  .star {
    width: 10px;
    height: 10px;
  }

  .hero > .star {
    width: 14px;
    height: 14px;
  }
}

@media screen and (min-width: 1280px) {
  .star {
    width: 18px;
    height: 18px;
  }

  .hero > .star {
    width: 14px;
    height: 14px;
  }
}

.scroll-up-btn {
  z-index: 99;
  background-color: inherit;
  cursor: pointer;
  border: none;
  padding: 15px;
  transition: transform .5s;
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
  transform: scale(1);
}

.scroll-up-btn:hover {
  transform: scale(1.35);
}

.movies-container {
  flex-flow: column wrap;
  align-content: center;
  row-gap: 16px;
  display: flex;
}

.movie-card {
  width: 280px;
  height: 406px;
  border-radius: 5px;
  position: relative;
  overflow: hidden;
}

.movie-card__backdrop {
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0) 63.48%, rgba(0, 0, 0, .9) 92.16%);
  position: absolute;
  bottom: 0;
}

.movie-card__img {
  width: 100%;
}

.movie-card__info {
  width: 100%;
  padding: 0 12px 12px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.6;
  position: absolute;
  bottom: 0;
}

.movie-card__info__items {
  justify-content: space-between;
  align-items: flex-end;
  display: flex;
}

.info-item__name {
  text-transform: uppercase;
  color: var(--lightest-color);
}

.info-item__genreYear {
  color: var(--light-grey-color);
}

.info-item__stars {
  flex-shrink: 0;
  padding-bottom: 4px;
  padding-left: 12px;
  line-height: 1.67;
}

.catalog-failure {
  display: none;
}

@media screen and (min-width: 768px) {
  .gallery-section {
    padding-top: 36px;
  }

  .movies-container {
    flex-direction: row;
    gap: 28px 16px;
  }

  .movie-card {
    width: 224px;
    height: 325px;
  }

  .movie-card__info {
    line-height: 1.58;
  }

  .info-item__name {
    padding-bottom: 4px;
  }
}

@media screen and (min-width: 1280px) {
  .gallery-section {
    padding-top: 48px;
  }

  .movies-container {
    gap: 32px 15px;
  }

  .movie-card {
    width: 395px;
    height: 574px;
  }

  .movie-card__info {
    padding: 0 20px 20px;
    font-size: 20px;
    line-height: 1.15;
  }
}

.footer {
  text-align: center;
  color: var(--light-grey-color);
  background: inherit;
  padding-top: 40px;
  padding-bottom: 28px;
}

.footer__description {
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer__icon {
  fill: var(--accent-color);
  margin-left: 10px;
  margin-right: 10px;
  animation: animHeart 2s infinite forwards;
}

.footer__btn {
  color: var(--light-grey-color);
  background-color: rgba(0, 0, 0, 0);
  border-style: none;
  margin-left: 3px;
  padding: 0;
  position: relative;
}

.footer__btn:after, .footer__btn:before {
  content: "";
  width: 100%;
  height: 2px;
  transition: transform var(--transition-dur-and-fun);
  position: absolute;
  bottom: 0;
  left: 0;
}

.footer__btn:before {
  background-color: var(--accent-color);
  transform-origin: 0;
  transition-delay: 0s;
  transform: scaleX(0);
}

.footer__btn:after {
  background-color: var(--light-grey-color);
  transform-origin: 100%;
  transition-delay: .25s;
  transform: scaleX(1);
}

.footer__btn:hover:before, .footer__btn:focus:before {
  transition-delay: .25s;
  transform: scaleX(1);
}

.footer__btn:hover:after, .footer__btn:focus:after {
  transition-delay: 0s;
  transform: scaleX(0);
}

@media screen and (min-width: 768px) {
  .footer {
    padding-top: 60px;
    padding-bottom: 40px;
    font-size: 16px;
    line-height: 1.2;
  }

  .footer__wrapper {
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .footer__description:not(:last-child) {
    margin-right: 3px;
  }
}

@media screen and (min-width: 1280px) {
  .footer {
    padding-top: 100px;
    padding-bottom: 80px;
  }
}

@keyframes animHeart {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.3);
  }

  100% {
    transform: scale(1);
  }
}

.header {
  background-color: var(--dark-color);
}

.nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo__img {
  width: 32px;
  height: 32px;
}

.mob-menu-btn {
  text-transform: uppercase;
  color: var(--light-grey-color);
  background-color: rgba(0, 0, 0, 0);
  border-style: none;
  padding-top: 16px;
  padding-bottom: 16px;
  font-family: Roboto;
}

.theme.btn {
  min-width: 44px;
  max-height: 18px;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 4px 3px;
  display: flex;
  position: relative;
}

.theme__icon {
  width: 10px;
  height: 10px;
  stroke: var(--lightest-color);
  fill: none;
}

.theme__switch {
  width: 14px;
  height: 14px;
  transform: translate(-20%, -50%) rotate(0);
  position: absolute;
  top: 50%;
  right: 0;
}

.site-nav {
  display: flex;
}

.site-nav__item:not(:last-child) {
  margin-right: 32px;
}

.site-nav__link {
  text-transform: uppercase;
  color: var(--light-grey-color);
  transition: color var(--transition-dur-and-fun);
}

.site-nav__link:hover, .site-nav__link:focus, .js-nav-link.active-page {
  color: var(--accent-color);
}

.logo__wrapper {
  color: var(--lightest-color);
  align-items: center;
  display: flex;
}

.logo__name {
  margin-left: 8px;
  font-size: 24px;
  font-weight: 400;
  line-height: 1.2;
  display: none;
  position: relative;
  overflow: hidden;
}

.logo__name:after {
  content: "";
  width: 100%;
  height: 2px;
  transform-origin: 100%;
  background-color: var(--accent-color);
  transition: transform 1s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
}

.mobile-backdrop {
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  transition: background-color var(--transition-dur-and-fun);
  background: rgba(255, 255, 255, .2);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}

.mobile-backdrop.is-open {
  opacity: 1;
  pointer-events: all;
  overflow: auto;
}

.mobile-nav {
  width: 65%;
  height: 100vh;
  background-color: var(--dark-color);
  opacity: 0;
  transition: transform var(--transition-dur-and-fun), opacity var(--transition-dur-and-fun);
  padding-top: 8px;
  padding-left: 20px;
  transform: translateY(-100%);
}

.mobile-backdrop.is-open .mobile-nav {
  opacity: 1;
  transform: translateY(0);
}

.mobile__logo {
  margin-bottom: 40px;
}

.mobile-menu__item:not(:last-child) {
  margin-bottom: 24px;
}

.mobile-menu__link {
  color: var(--lightest-color);
}

@media screen and (max-width: 767px) {
  .site-nav {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .nav {
    padding-top: 6px;
    padding-bottom: 6px;
  }

  .logo__img {
    width: 48px;
    height: 48px;
  }

  .logo__name {
    display: block;
  }

  .mob-menu-btn {
    display: none;
  }

  .theme.btn {
    min-width: 60px;
    min-height: 24px;
    padding: 5px 4px;
  }

  .theme__icon {
    width: 13px;
    height: 13px;
  }

  .theme__switch {
    width: 18px;
    height: 18px;
  }

  .mobile-backdrop {
    display: none;
  }

  @keyframes animSwitchLight {
    0% {
      transform: translate(-15%, -50%)rotate(0deg);
    }

    100% {
      transform: translate(-215%, -50%)rotate(-180deg);
    }
  }

  @keyframes animSwitchDark {
    0% {
      transform: translate(-215%, -50%)rotate(-180deg);
    }

    100% {
      transform: translate(-15%, -50%)rotate(0deg);
    }
  }
}

@media screen and (min-width: 1280px) {
  .site-nav__item:not(:last-child) {
    margin-right: 40px;
  }

  .logo__wrapper:hover .logo__name:after {
    transform-origin: 0;
    transform: scaleX(1);
  }
}

.switchLight {
  animation: animSwitchLight .5s cubic-bezier(.215, .61, .355, 1) forwards;
}

.switchDark {
  animation: animSwitchDark .5s cubic-bezier(.215, .61, .355, 1) forwards;
}

@keyframes animSwitchLight {
  0% {
    transform: translate(-15%, -50%)rotate(0deg);
  }

  100% {
    transform: translate(-200%, -50%)rotate(-180deg);
  }
}

@keyframes animSwitchDark {
  0% {
    transform: translate(-190%, -50%)rotate(-180deg);
  }

  100% {
    transform: translate(-15%, -50%)rotate(0deg);
  }
}



